"use client";

import { useState } from "react";
import { X, Calendar as CalendarIcon, Save } from "lucide-react";
import { clsx } from "clsx";
import { createClient } from "@/utils/supabase/client";
import { useRouter } from "next/navigation";

export default function TransactionForm({ isOpen, onClose, userId }: { isOpen: boolean; onClose: () => void; userId: string }) {
    const [type, setType] = useState<"income" | "expense">("expense");
    const [amount, setAmount] = useState("");
    const [category, setCategory] = useState("Sin categoría");
    const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
    const [note, setNote] = useState("");
    const [loading, setLoading] = useState(false);
    const router = useRouter();
    const supabase = createClient();

    if (!isOpen) return null;

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        // 1. Get or Create Category (Basic Implementation: Just use ID if storing, but here we simplify)
        // For MVP, we insert raw category name or fetch ID. Let's just mock category ID fetch or insert simple
        // Ideally we should select from existing categories.
        // Let's assume we just insert into 'transactions' now.

        // NOTE: In a real app we should fetch categories first. 
        // For this demo speed, we will assume a generic 'uncategorized' or just insert without FK constraint strictness if possible,
        // BUT we have FK constraints.
        // Solution: We will fetch the 'General' category for the user or create it.

        // Quick Fix: Create a 'General' category for the user if it doesn't exist.
        const { data: categories } = await supabase.from('categories').select('id').eq('user_id', userId).eq('name', 'General').maybeSingle();
        let categoryId = categories?.id;

        if (!categoryId) {
            const { data: newCat } = await supabase.from('categories').insert({
                user_id: userId,
                name: 'General',
                type: 'expense'
            }).select().single();
            categoryId = newCat?.id;
        }

        const { error } = await supabase.from('transactions').insert({
            user_id: userId,
            category_id: categoryId,
            amount: parseFloat(amount),
            date: date,
            note: note,
            // We need to handle type (income/expense) maybe in category or negative amount? 
            // Sikai Logic: Expenses are usually positive numbers in DB but categorized as expense. 
            // Let's stick to positive and use Type logic in query.
        });

        if (error) {
            console.error(error);
            alert("Error al guardar");
        } else {
            router.refresh(); // Refresh server components
            onClose();
            setAmount("");
            setNote("");
        }
        setLoading(false);
    };

    return (
        <div className="fixed inset-0 z-50 flex justify-end">
            <div className="absolute inset-0 bg-black/50 backdrop-blur-sm" onClick={onClose} />

            <div className="relative w-full max-w-md bg-card h-full p-6 shadow-2xl border-l border-border transform transition-transform animate-in slide-in-from-right duration-300">
                <div className="flex justify-between items-center mb-6">
                    <h2 className="text-xl font-headline font-bold">Nueva Transacción</h2>
                    <button onClick={onClose} className="p-2 hover:bg-muted rounded-full">
                        <X size={24} />
                    </button>
                </div>

                <form className="space-y-6" onSubmit={handleSubmit}>
                    <div className="flex bg-muted p-1 rounded-lg">
                        <button
                            type="button"
                            onClick={() => setType("expense")}
                            className={clsx(
                                "flex-1 py-2 rounded-md text-sm font-medium transition-colors",
                                type === "expense" ? "bg-red-500/20 text-red-500 shadow-sm" : "text-muted-foreground hover:text-foreground"
                            )}
                        >
                            Gasto
                        </button>
                        <button
                            type="button"
                            onClick={() => setType("income")}
                            className={clsx(
                                "flex-1 py-2 rounded-md text-sm font-medium transition-colors",
                                type === "income" ? "bg-green-500/20 text-green-500 shadow-sm" : "text-muted-foreground hover:text-foreground"
                            )}
                        >
                            Ingreso
                        </button>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm text-muted-foreground">Monto</label>
                        <div className="relative">
                            <span className="absolute left-4 top-1/2 -translate-y-1/2 text-muted-foreground">$</span>
                            <input
                                type="number"
                                value={amount}
                                onChange={e => setAmount(e.target.value)}
                                placeholder="0.00"
                                className="w-full bg-muted/50 border border-border rounded-lg py-3 pl-8 pr-4 text-2xl font-mono focus:outline-none focus:ring-2 focus:ring-primary/50"
                                required
                            />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm text-muted-foreground">Categoría</label>
                        <select
                            value={category}
                            onChange={e => setCategory(e.target.value)}
                            className="w-full bg-muted/50 border border-border rounded-lg py-3 px-4 appearance-none focus:outline-none focus:ring-2 focus:ring-primary/50 text-foreground"
                        >
                            <option value="General">General</option>
                            <option value="Comida">Comida</option>
                            <option value="Transporte">Transporte</option>
                            <option value="Vivienda">Vivienda</option>
                            <option value="Ocio">Ocio</option>
                        </select>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm text-muted-foreground">Fecha</label>
                        <div className="relative">
                            <input
                                type="date"
                                value={date}
                                onChange={e => setDate(e.target.value)}
                                className="w-full bg-muted/50 border border-border rounded-lg py-3 pl-4 pr-10 appearance-none focus:outline-none focus:ring-2 focus:ring-primary/50 text-white fill-white"
                            />
                            <CalendarIcon className="absolute right-4 top-1/2 -translate-y-1/2 text-muted-foreground pointer-events-none" size={20} />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm text-muted-foreground">Nota (Opcional)</label>
                        <textarea
                            rows={3}
                            value={note}
                            onChange={e => setNote(e.target.value)}
                            className="w-full bg-muted/50 border border-border rounded-lg p-3 focus:outline-none focus:ring-2 focus:ring-primary/50"
                            placeholder="Detalles adicionales..."
                        />
                    </div>

                    <button type="submit" disabled={loading} className="w-full bg-primary hover:bg-primary/90 text-primary-foreground py-4 rounded-lg font-bold flex items-center justify-center gap-2 mt-8 transition-colors">
                        <Save size={20} />
                        {loading ? "Guardando..." : "Guardar Transacción"}
                    </button>
                </form>
            </div>
        </div>
    );
}
