"use client";

import { PieChart, Pie, Cell, ResponsiveContainer, BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid } from "recharts";
import { ArrowUpRight, ArrowDownRight, Wallet, Target, Layers, Calendar } from "lucide-react";
import { createClient } from "@/utils/supabase/client";
import { useEffect, useState } from "react";
import { clsx } from "clsx";

export default function Dashboard({ userId }: { userId: string }) {
    const [balance, setBalance] = useState(0);
    const [income, setIncome] = useState(0);
    const [expense, setExpense] = useState(0);
    const [loading, setLoading] = useState(true);
    const [timeFilter, setTimeFilter] = useState<'month' | 'year'>('month');

    // Datos dummy mejorados para demo Tech
    const [chartData, setChartData] = useState([
        { name: "Gastos", value: 1200 },
        { name: "Disponible", value: 3000 },
    ]);

    useEffect(() => {
        const fetchData = async () => {
            const supabase = createClient();

            // Simulación de filtro por fecha (en implementación real usaríamos ranges)
            const { data: transactions } = await supabase
                .from('transactions')
                .select('*')
                .eq('user_id', userId);

            if (transactions) {
                let inc = 0;
                let exp = 0;

                transactions.forEach(t => {
                    // Lógica temporal simple: Asumimos todo es Gasto por falta de campo Type en primera versión
                    // En V2 DB update deberíamos arreglar esto, pero por fidelidad al UI request primero:
                    exp += Number(t.amount);
                });

                // Mock Income para que se vea bien el grafico
                inc = 5000;

                setIncome(inc);
                setExpense(exp);
                setBalance(inc - exp);
            }
            setLoading(false);
        };
        fetchData();
    }, [userId, timeFilter]);

    const COLORS = ["#1a88ff", "#261c2c"]; // Tech Blue vs Dark

    return (
        <div className="p-4 space-y-8 pb-32">

            {/* Time Filter Tabs */}
            <div className="flex justify-center mb-6">
                <div className="bg-muted/30 p-1 rounded-full border border-glass-border backdrop-blur-md flex">
                    <button
                        onClick={() => setTimeFilter('month')}
                        className={clsx("px-6 py-2 rounded-full text-sm font-medium transition-all duration-300", timeFilter === 'month' ? "bg-primary text-white shadow-neon" : "text-muted-foreground hover:text-white")}
                    >
                        Mensual
                    </button>
                    <button
                        onClick={() => setTimeFilter('year')}
                        className={clsx("px-6 py-2 rounded-full text-sm font-medium transition-all duration-300", timeFilter === 'year' ? "bg-primary text-white shadow-neon" : "text-muted-foreground hover:text-white")}
                    >
                        Anual
                    </button>
                </div>
            </div>

            {/* Hero Stats */}
            <section className="grid grid-cols-1 md:grid-cols-3 gap-6">
                {/* Main Balance */}
                <div className="glass-card p-8 relative overflow-hidden group">
                    <div className="absolute top-0 right-0 p-4 opacity-10 group-hover:opacity-20 transition-opacity">
                        <Wallet size={120} />
                    </div>
                    <div className="relative z-10">
                        <h3 className="text-muted-foreground font-medium flex items-center gap-2 mb-2">
                            <span className="w-2 h-2 rounded-full bg-primary-cyan animate-pulse"></span>
                            Saldo Disponible
                        </h3>
                        <p className="text-5xl font-bold font-mono text-white text-glow tracking-tighter">
                            {loading ? "..." : `$ ${balance.toLocaleString()}`}
                        </p>
                        <div className="mt-4 flex gap-2">
                            <span className="text-xs bg-primary/20 text-primary px-2 py-1 rounded border border-primary/20">
                                +12% vs mes anterior
                            </span>
                        </div>
                    </div>
                </div>

                {/* Income */}
                <div className="glass-card p-6 flex flex-col justify-center">
                    <p className="text-muted-foreground text-sm flex items-center gap-2 mb-1">
                        <ArrowUpRight size={16} className="text-primary-cyan" /> Ingresos
                    </p>
                    <p className="text-3xl font-bold font-mono text-white neon-text-cyan">
                        $ {income.toLocaleString()}
                    </p>
                    <div className="w-full bg-muted/50 h-1 mt-4 rounded-full overflow-hidden">
                        <div className="bg-primary-cyan h-full w-[70%] shadow-[0_0_10px_#26d8c4]"></div>
                    </div>
                </div>

                {/* Expense */}
                <div className="glass-card p-6 flex flex-col justify-center">
                    <p className="text-muted-foreground text-sm flex items-center gap-2 mb-1">
                        <ArrowDownRight size={16} className="text-pink-500" /> Gastos
                    </p>
                    <p className="text-3xl font-bold font-mono text-white" style={{ textShadow: "0 0 10px rgba(236, 72, 153, 0.5)" }}>
                        $ {expense.toLocaleString()}
                    </p>
                    <div className="w-full bg-muted/50 h-1 mt-4 rounded-full overflow-hidden">
                        <div className="bg-pink-500 h-full w-[45%] shadow-[0_0_10px_#ec4899]"></div>
                    </div>
                </div>
            </section>

            {/* Analytics Grid */}
            <section className="grid grid-cols-1 lg:grid-cols-3 gap-6">

                {/* Budget Circle */}
                <div className="glass-card p-6 lg:col-span-1 flex flex-col items-center justify-center relative">
                    <h3 className="text-lg font-headline font-semibold mb-2 w-full text-left flex items-center gap-2">
                        <Target size={18} className="text-primary" /> Ejecución Presupuestal
                    </h3>
                    <div className="h-64 w-full relative">
                        <ResponsiveContainer width="100%" height="100%">
                            <PieChart>
                                <Pie
                                    data={chartData}
                                    cx="50%"
                                    cy="50%"
                                    innerRadius={70}
                                    outerRadius={90}
                                    paddingAngle={2}
                                    dataKey="value"
                                    stroke="none"
                                >
                                    <Cell key="cell-0" fill="#ec4899" style={{ filter: "drop-shadow(0px 0px 5px rgba(236, 72, 153, 0.5))" }} />
                                    <Cell key="cell-1" fill="#1a88ff" style={{ opacity: 0.2 }} />
                                </Pie>
                            </PieChart>
                        </ResponsiveContainer>
                        <div className="absolute inset-0 flex items-center justify-center flex-col pointer-events-none">
                            <span className="text-4xl font-bold font-mono text-white">40%</span>
                            <span className="text-xs text-muted-foreground">Gastado</span>
                        </div>
                    </div>
                </div>

                {/* Cash Flow Bar Chart */}
                <div className="glass-card p-6 lg:col-span-2">
                    <h3 className="text-lg font-headline font-semibold mb-6 flex items-center gap-2">
                        <Calendar size={18} className="text-primary" /> Flujo Financiero
                    </h3>
                    <div className="h-64 cursor-crosshair">
                        <ResponsiveContainer width="100%" height="100%">
                            <BarChart data={[
                                { name: "S1", inc: 4000, exp: 2400 },
                                { name: "S2", inc: 3000, exp: 1398 },
                                { name: "S3", inc: 2000, exp: 9800 },
                                { name: "S4", inc: 2780, exp: 3908 },
                            ]}>
                                <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="rgba(255,255,255,0.05)" />
                                <XAxis dataKey="name" stroke="#4b5563" fontSize={12} tickLine={false} axisLine={false} />
                                <YAxis stroke="#4b5563" fontSize={12} tickLine={false} axisLine={false} tickFormatter={(value) => `$${value / 1000}k`} />
                                <Tooltip
                                    contentStyle={{
                                        backgroundColor: 'rgba(22, 24, 29, 0.9)',
                                        borderColor: 'rgba(26, 136, 255, 0.2)',
                                        backdropFilter: 'blur(10px)',
                                        borderRadius: '12px',
                                        boxShadow: '0 0 15px rgba(0,0,0,0.5)'
                                    }}
                                    itemStyle={{ color: '#fff', fontFamily: 'monospace' }}
                                    cursor={{ fill: 'rgba(255,255,255,0.03)' }}
                                />
                                <Bar dataKey="inc" fill="#26d8c4" radius={[4, 4, 0, 0]} maxBarSize={40} />
                                <Bar dataKey="exp" fill="#ec4899" radius={[4, 4, 0, 0]} maxBarSize={40} />
                            </BarChart>
                        </ResponsiveContainer>
                    </div>
                </div>
            </section>

            {/* Feature Access Grid */}
            <section className="grid grid-cols-2 gap-4">
                <button className="glass-card p-4 flex flex-col items-center justify-center gap-2 hover:bg-primary/10 group">
                    <div className="p-3 rounded-full bg-primary/10 text-primary group-hover:shadow-neon transition-all">
                        <Target size={24} />
                    </div>
                    <span className="text-sm font-medium">Nuevo Presupuesto</span>
                </button>
                <button className="glass-card p-4 flex flex-col items-center justify-center gap-2 hover:bg-primary/10 group">
                    <div className="p-3 rounded-full bg-primary/10 text-primary-cyan group-hover:shadow-neon-cyan transition-all">
                        <Layers size={24} />
                    </div>
                    <span className="text-sm font-medium">Categorías</span>
                </button>
            </section>
        </div>
    );
}
